h1 {
    color: #FFF;
    font-size: 32px;
    margin-top: 0;
    margin-bottom: 0;
}

p {
    color: #FFF;
    font-size: 24px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-top: 0;
    margin-bottom: 0;
}

p.underLine {
    text-decoration-line: underline;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

/* Outline */
.sh1 {
     text-shadow:
             -1px -1px 3px #119D5A,
             1px -1px 3px #119D5A,
             -1px 1px 3px #119D5A,
             1px 1px 3px #119D5A;
 }

.sh2 {
    text-shadow:
            -1px -1px 3px #4285F5,
            1px -1px 3px #4285F5,
            -1px 1px 3px #4285F5,
            1px 1px 3px #4285F5;
}

.sh3 {
    text-shadow:
            -1px -1px 3px #FCBC05,
            1px -1px 3px #FCBC05,
            -1px 1px 3px #FCBC05,
            1px 1px 3px #FCBC05;
}